import { mapState, mapGetters } from "vuex";
import appTimeScreening from "../../../components/page-component/app-time-screening/app-time-screening.vue";
import appMenu from '../components/app-menu';
import appOrder from '../components/app-order';

export default {
    data() {
        return {
            _num: '0',
            getFocus: false,
            more_list: false,
            page: 1,
            menu: [
                {
                    name: '待提货',
                    value: '3'
                },
                {
                    name: '已提货',
                    value: '4'
                },
                {
                    name: '已完成',
                    value: '8'
                },
                {
                    name: '已关闭',
                    value: '6'
                }
            ],
            active: null,
            is_user: false,
            show: false,
            search: false,
            keyword: '',
            list: [],
            candidate: [],
            date_start: '',
            date_end: '',
            activeTab: -1,
            time: 0,
            activity_id: 0,
            windowHeight: 0,
            chooseTime: false,
            detail: {},
            custom: false,
            msg: false,
            searchResult: false,
            loading: false
        }
    },
    components: {
        "app-menu": appMenu,
        'app-order': appOrder,
        "app-time-screening": appTimeScreening
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin,
        })
    },
    methods: {
        cancel() {
            this.chooseTime = false;
        },
        update(e) {
            for(let index in this.list) {
                if(this.list[index].id == e.id) {
                    this.list[index] = e;
                }
            }
            this.$forceUpdate();
        },
        clearSearch() {
            this.keyword = '';
        },
        toSearch() {
            this.search = true;
            this.getFocus = true;
            this.list = [];
            this.searchResult = false;
            this.candidate = this.$storage.getStorageSync('keyword') ? this.$storage.getStorageSync('keyword') : [];
        },
        keywordSearch(e) {
            this.keyword = e;
            this.searchResult = true;
            this.search = false;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.page = 1;
            this.getList();
        },
        // 清除搜索记录
        clear() {
            let that = this;
            this.$storage.removeStorageSync('keyword');
            that.candidate = [];
            uni.showToast({
                title: '清理成功',
                duration: 1000,
                type: 'success',
                mask: false
            });
        },
        // 搜索
        searchMethod() {
            let value = this.$storage.getStorageSync('keyword') ? this.$storage.getStorageSync('keyword') : [];
            if (this.keyword.length == 0) {
                return
            } else if (value.length > 0) {
                value.unshift(this.keyword)
            } else {
                value = [this.keyword]
            }
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.page = 1;
            this.getList();
            value.forEach(function(row, index) {
                if (value[0] == value[index] && index > 0) {
                    value.splice(index, 1)
                }
            })
            this.$storage.setStorageSync('keyword', value);
            this.search = false;
            this.searchResult = true;
        },
        // 取消搜索
        cancelSeacrch() {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.search = false;
            this.keyword = '';
            // this.list = [];
            this.page = 1;
            this.getList();
        },
        change(e) {
            let that = this;
            that.time = e;
            that.custom = false;
            if (that.time == 4) {
                that.custom = true;
                that.date_end = that.today;
                that.date_start = that.today + ' 00:00:00';
            }
        },
        // 确认选择时间
        toChoose(res) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.date_start = res.date_start;
            that.date_end = res.date_end;
            that.time = res.choose;
            that.page = 1;
            that.getList();
            that.chooseTime = false;
        },
        // 打开时间筛选
        toTime() {
            let that = this;
            that.chooseTime = !that.chooseTime;
            that.show = false;
        },
        // 选择更多状态
        chooseItem(e) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that._num = e;
            that.menu.forEach(function(row, index) {
                if (row.value == that._num) {
                    that.active = row;
                }
            })
            that.show = !that.show;
            that.page = 1;
            that.list = [];
            that.getList();
        },
        // 切换状态
        tab(e) {
            this._num = e;
            this.show = false;
            this.active = null;
            this.list = [];
            this.page = 1;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },
        getList() {
            let that = this;
            if(that.loading) {
                return false
            }
            that.loading = true;
            that.$request({
                url: that.is_user ? that.$api.community.order_list : that.$api.community.middle_list,
                data: {
                    status: that._num,
                    date_end: that.date_end,
                    date_start: that.date_start,
                    page: that.page,
                    keyword: that.keyword
                }
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                that.loading = false;
                if(response.code == 0) {
                    let list = response.data.list;
                    that.more_list = false;
                    if (list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                    if(that.page == 1) {
                        that.list = response.data.list;
                        that.$forceUpdate();
                    }else {
                        that.list = that.list.concat(list);
                        that.$forceUpdate();
                    }
                    that.page++;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.loading = false;
                that.$hideLoading();
                uni.hideLoading();
            })
        },
        getMore() {
            if (this.more_list) {
                this.getList();
            }
        }
    },
    onShow: function() {
        this.page = 1;
        this.getList();
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if(options.is_user == 1) {
            that.is_user = true
        }
        if(options.keyword) {
            that.keyword = options.keyword;
        }
        uni.getSystemInfo({
            success: function (res) {
                that.windowHeight = res.windowHeight;
            }
        })
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that._num = 0
    }
}
